.profile {
    position: relative;
    height: auto;
    width: 100%;
    padding-top: 550px;
    justify-content: center;
}

.profile h4 {
    padding-top: 460px;
    padding-left: 18%;
    padding-right: 18%;
    font-size: 180px;
    font-weight: bolder;
    color: #c74829;
    font-family: 'overlock';
}

.profile h2 {
    padding-top: 70px;
    padding-left: 18%;
    padding-right: 18%;
    font-size: 70px;
    font-weight: bold;
    color: #da5533;
}

.profile h3 {
    padding-top: 250px;
    padding-left: 20%;
    padding-right: 20%;
    padding-bottom: 260px;
    margin-top: 10px;
    font-size: 110px;
    font-weight: bold;
    color: #da5533;
}

.profile button {
    height: 200px;
    width: 200px;
    margin-bottom: 150px;
    margin-top: 100px;
    background-color: #8f2e23;
    border-radius: 200px;
    border-color: #bd4436;
    border-style: solid;
    border-width: 15px;
    animation: button 4s infinite;
    animation-timing-function: ease;
    transition: 0.5s;
    transition-timing-function: ease;
}

@keyframes button {
    10%{
        border-color: #bd4436;
        border-width: 15px; 
    }
    50%{
        border-color: #fd998e;
        border-width: 35px;
    }
    100%{border-color: #bd4436;
        border-width: 15px;
    }
}

.profile button:hover {
    margin-top: 20px;
    height: 300px;
    width: 300px;
    background-color: #c9574b;
    border-color: #a8372b;
    border-style: solid;
    border-width: 20px; 
    animation: buttonHover 4s infinite;  
}

@keyframes buttonHover {
    10%{
        border-color: #a8372b;
        border-width: 20px;
        background-color: #c9574b; 
    }
    50%{
        border-color: #ffbab2;
        border-width: 45px;
        background-color: #aa3e31;
    }
    100%{border-color: #a8372b;
        border-width: 20px;
        background-color: #c9574b;
    }
}

.profile button a {
    font-size: 100px;
    text-decoration: none;
    color: transparent;
}

