.info {
    height: 150px;
    width: 150px;
    margin-bottom: 150px;
    margin-top: 100px;
    background-color: #8f2e23;
    border-radius: 200px;
    border-color: #bd4436;
    border-style: solid;
    border-width: 15px;
    animation: info 4s infinite;
    animation-timing-function: ease;
    transition: 0.5s;
    transition-timing-function: ease;
}

@keyframes info {
    10%{
        border-color: #bd4436;
        border-width: 20px; 
    }
    50%{
        border-color: #ffb4ac;
        border-width: 20px;
    }
    100%{border-color: #bd4436;
        border-width: 20px;
    }
}

.info:hover {
    margin-top: 20px;
    height: 200px;
    width: 200px;
    background-color: #c9574b;
    border-color: #a8372b;
    border-style: solid;
    border-width: 20px; 
    animation: infoHover 4s infinite;  
}

@keyframes infoHover {
    10%{
        border-color: #a8372b;
        border-width: 40px;
        background-color: #c9574b; 
    }
    50%{
        border-color: #ffccc6;
        border-width: 40px;
        background-color: #962a1e;
    }
    100%{border-color: #a8372b;
        border-width: 40px;
        background-color: #c9574b;
    }
}

.info a {
    font-size: 100px;
    text-decoration: none;
    color: transparent;
}


.info-end {
    height: 80px;
    width: 80px;
    /* margin-bottom: 150px;
