        border-width: 20px; 
    }
    50%{
        border-color: #ffb4ac;
        border-width: 20px;
    }
    100%{border-color: #bd4436;
        border-width: 20px;
    }
}

.info-end:hover {
    /* margin-top: 20px; */
    height: 130px;
