    .inner.show{
        height: 10%;
        width: 100%;
    }

    .work-info-contain{
        overflow: hidden;
        display: block;
        opacity: 1;
        transition: 1s;
        transition-timing-function: ease;
        transition-delay: 1s;
    }

    .work-info-contain.show {
        display: none;
        opacity: 0;
        transition: 0.7s;
        transition-timing-function: ease-out;
    }

    .animate-button-contain {
        padding-right: 0;

    }

    .animate-button.animate-button-class {
        display: flex;
    }

    .others-grid {
        display: grid;
        grid-template-columns: 1fr;
        /* grid-template-rows: 1fr 1fr 1fr; */
        grid-gap: 100px;
        margin: 0;
        padding: 0;
        list-style: none;
      }
}

